* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font-family, CircularSp, CircularSp-Arab, CircularSp-Hebr, CircularSp-Cyrl, CircularSp-Grek, CircularSp-Deva, var(--fallback-fonts, sans-serif));
}

a {
  text-decoration: none;
  color: #fff;
}

body {
  background-color: black;
  /* padding: 20px; */
  overflow: hidden;
  color: #fff;
  position: relative;
  height: 100vh;
}

.main {
  height: 100%;
  display: flex;
  /* align-items: center;
  justify-content: center; */
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  /* margin-bottom: 20px; */
}

.recordList {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.recordList .record {
  background-color: rebeccapurple;
  padding: 20px;
  border-radius: 15px;
  margin-bottom: 18px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.recordList .record .name {
  font-size: 24px;
  font-weight: bold;
}


#menuButton {
  font-size: 25px;
  align-self: flex-end;
  display: flex;
  position: absolute;
  right: 0;
}

#sideBar {
  /* width: 100%; */
  height: 100%;
  /* position: absolute; */
  top: 0;
  right: 0px;
  transition: ease-in-out;
  /* display: none; */
}

#sideBar .content {
  color: black;
  background-color: white;
  padding: 20px;
  height: 100vh;
  width: 70%;
  min-width: 270px;
  max-width: 70vw;
}

/* #sideBar .contentSpacer {
  width: 100%;
  height: 100%;
  background-color: #000000ab;
} */

#sideBar .header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

#sideBar .header .close {
  font-size: 35px;
  display: flex;
  display: none;
}

.menueList {
  list-style: none;
  display: flex;
  flex-direction: column;
}

.menueList .menu {
  font-size: 18px;
  padding: 10px;
  margin: 0 0 10px;
  background-color: rgba(128, 128, 128, 0.4);
  border-radius: 10px;
  color: black;
  display: flex;
  align-items: center;
  justify-content: left;
  gap: 10px;
}

.loginForm {
  display: flex;
  background-color: rebeccapurple;
  /* height: 50vh; */
  width: 90vw;
  max-width: 450px;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-radius: 20px;
}
.loginForm form{
  display: flex;
  flex-direction: column;
}

.loginForm .title {
  font-size: 35px;
}

.loginForm form label {
  font-size: 18px;
  color: #ffffff90;
}

.loginForm input {
  color: #fff;
  font-size: 24px;
  background: none;
  border: none;
  outline: none;
  border-bottom: 2px solid white;
  margin-bottom: 35px;
  padding: 7px;
}

.loginForm button[type="submit"]{
  margin: 20px 0;
  padding: 7px;
  font-size: 18px;
  border: 2px solid white;
  border-radius: 50px;
  color: rebeccapurple;
}